/* Fixed/sticky icon bar (vertically aligned 50% from the top of the screen) */
.icon-bar {
  position: fixed;
  top: 78%;
  right:1%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
.gmail-bar {
  position: fixed;
  top: 68%;
  left:1%;
  font-size:10px !important;
